import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    image: {},
    payment: {}
  },
  mutations: {
    setImage(state, image) {
      state.image = image;
    },
    setPayment(state, payment) {      
      state.payment = payment;
    }
  },
  getters: {
    image(state) {
      return state.image
    },
    payment(state) {
      return state.payment;
    }
  }
})
