<template>
    <div class="payment_details">
        <div id="qrcode">
            <canvas ref="canvas" id="canvas"></canvas>
        </div>
        <div id="details">
            <div>Comercio:</div><div class="right">{{ ' ' + $store.state.payment.commerce }}</div>
            <div>Monto a pagar:</div><div class="right">{{ ' $' + ($store.state.payment.amount / 100).toFixed(2) }}</div>
            <div>Concepto:</div><div class="right">{{ ' ' + $store.state.payment.concept }}</div>
        </div>
        <div id="buttons">
            <div class="button flat red">CANCELAR</div>
            <div class="button green">PAGAR</div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'PaymentDetails',
    data: function() {
        return {
            canvas_tag: {},
            canvas: null
        }
    },
    mounted: function() {        
        this.canvas_tag = this.$refs.canvas;
        this.canvas_tag.height = this.$store.state.image.height;
        this.canvas_tag.width = this.$store.state.image.width;
        this.canvas = this.canvas_tag.getContext("2d");
        this.canvas.putImageData(this.$store.state.image , 0, 0);
    }
}
</script>

<style>

.payment_details {
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-rows: 40% 50% 10%;
    align-items: center;
}

#qrcode {
    background-color: black;
    width: 100%;
    height: 100%;
}

#buttons {
    display: grid;
    grid-template-columns: 50% 50%;
    text-align: center;
    font-size: 1.5em;
}

#details {
    display: grid;
    grid-template-columns: 40% 60%;
    margin: 5%;
    font-size: 20px;
}

.right {
    text-align: right;
}

.button {
    margin: 5%;
    padding: 10px;
}

.green {
    background-color: green;
    color: white;
}

.red {
    color: red;
}
</style>
